@import '../variables';

.Page {
    //position: absolute;
    //top: 0;
    //left: 0;
    position: relative;
    background: $pageBackground;

    width: 100%;
    height: 1080px;

    flex: 0 0 auto;
}



.ContentContainer {
    padding: 100px 80px;
    font-family: $fontLight;

    h2 {
        color: $red;
        font-size: 36px;
        line-height: 46px;
        margin-bottom: 40px;
    }

    p {
        font-size: 26px;
        line-height: 36px;
    }
}

.ImageCaption {
    h3 {
        text-transform: none;
        margin-bottom: 8px;
        font-size: 20px;
    }

    p {
        font-size: 16px;
    }
}








